import React from 'react';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import InsightsScreen from '../screens/InsightsScreen';
import InsightDetailsScreen from '../screens/InsightDetailsScreen';

export type MiniAppStackParamList = {
  Insights: undefined;
  InsightDetails: {insightId: string};
};

const Stack = createNativeStackNavigator<MiniAppStackParamList>();

export type MainNavigatorProps = {
  onClose?: () => void;
};

const MainNavigator = ({onClose}: MainNavigatorProps) => {
  return (
    <Stack.Navigator>
      <Stack.Screen name="Insights" options={{title: 'Daily Insights'}}>
        {props => <InsightsScreen {...props} onClose={onClose} />}
      </Stack.Screen>
      <Stack.Screen name="InsightDetails" options={{title: 'Chi tiết'}}>
        {props => <InsightDetailsScreen {...props} onClose={onClose} />}
      </Stack.Screen>
    </Stack.Navigator>
  );
};

export default MainNavigator;
