import React, {useMemo} from 'react';
import {StyleSheet, View} from 'react-native';
import {Text, Button} from 'react-native-paper';
import {NativeStackScreenProps} from '@react-navigation/native-stack';
import {MiniAppStackParamList} from '../navigation/MainNavigator';
import {insights, moodColors} from '../data/insights';

type InsightDetailsScreenProps = NativeStackScreenProps<
  MiniAppStackParamList,
  'InsightDetails'
> & {
  onClose?: () => void;
};

const InsightDetailsScreen = ({route, navigation, onClose}: InsightDetailsScreenProps) => {
  const {insightId} = route.params;
  const insight = useMemo(
    () => insights.find(item => item.id === insightId),
    [insightId],
  );

  if (!insight) {
    return (
      <View style={styles.missingContainer}>
        <Text>Không tìm thấy nội dung.</Text>
        <Button
          mode="contained"
          onPress={() => navigation.pop()}
          style={styles.button}>
          Quay lại
        </Button>
      </View>
    );
  }

  return (
    <View style={styles.container}>
      <View style={[styles.header, {backgroundColor: moodColors[insight.mood]}]}>
        <Text style={styles.headerLabel}>{insight.mood.toUpperCase()}</Text>
        <Text style={styles.title}>{insight.title}</Text>
      </View>
      <Text style={styles.summary}>{insight.summary}</Text>
      <Text style={styles.details}>{insight.details}</Text>
      <View style={styles.actions}>
        <Button
          mode="outlined"
          onPress={() => navigation.pop()}
          style={styles.button}>
          Quay lại danh sách
        </Button>
        <Button mode="contained" onPress={onClose} style={styles.button}>
          Trở về Super App
        </Button>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 24,
    backgroundColor: '#fff',
    justifyContent: 'flex-start',
  },
  header: {
    borderRadius: 16,
    padding: 16,
    marginBottom: 16,
  },
  headerLabel: {
    color: '#fff',
    fontWeight: '600',
    letterSpacing: 1,
  },
  title: {
    fontSize: 22,
    fontWeight: '700',
    color: '#fff',
    marginTop: 8,
  },
  summary: {
    fontSize: 16,
    fontWeight: '500',
    marginBottom: 12,
    color: '#0f172a',
  },
  details: {
    fontSize: 16,
    lineHeight: 22,
    color: '#475569',
  },
  button: {
    borderRadius: 999,
    marginTop: 16,
  },
  actions: {
    marginTop: 24,
    gap: 12,
  },
  missingContainer: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    padding: 24,
  },
});

export default InsightDetailsScreen;
