import React from 'react';
import {FlatList, StyleSheet, View} from 'react-native';
import {useNavigation} from '@react-navigation/native';
import {Card, Chip, Text, Button} from 'react-native-paper';
import {insights, moodColors, Insight} from '../data/insights';
import {MiniAppStackParamList} from '../navigation/MainNavigator';
import {NativeStackNavigationProp, NativeStackScreenProps} from '@react-navigation/native-stack';

type InsightsScreenProps = NativeStackScreenProps<MiniAppStackParamList, 'Insights'> & {
  onClose?: () => void;
};

type InsightCardProps = {
  item: Insight;
};

const InsightCard = ({item}: InsightCardProps) => {
  const navigation =
    useNavigation<NativeStackNavigationProp<MiniAppStackParamList>>();

  return (
    <Card
      style={styles.card}
      onPress={() => navigation.navigate('InsightDetails', {insightId: item.id})}
    >
      <Card.Title title={item.title} subtitle={item.summary} />
      <Card.Content>
        <Chip style={[styles.chip, {backgroundColor: moodColors[item.mood]}]}>
          <Text style={styles.chipText}>{item.mood.toUpperCase()}</Text>
        </Chip>
      </Card.Content>
    </Card>
  );
};

const InsightsScreen = ({onClose}: InsightsScreenProps) => {
  return (
    <View style={styles.container}>
      <Button
        mode="contained-tonal"
        icon="arrow-left"
        onPress={onClose}
        style={styles.backButton}
        labelStyle={styles.backButtonLabel}
      >
        Trở về Super App
      </Button>
      <FlatList
        data={insights}
        keyExtractor={item => item.id}
        renderItem={({item}) => <InsightCard item={item} />}
        contentContainerStyle={styles.listContent}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#f8fafc',
    padding: 16,
  },
  listContent: {
    paddingVertical: 8,
    gap: 12,
  },
  card: {
    borderRadius: 16,
  },
  chip: {
    alignSelf: 'flex-start',
    marginTop: 8,
  },
  chipText: {
    color: '#fff',
    fontWeight: '600',
  },
  backButton: {
    borderRadius: 999,
    alignSelf: 'flex-start',
  },
  backButtonLabel: {
    fontWeight: '600',
  },
});

export default InsightsScreen;
